* {
  margin: 0;
}
html,
body {
  height: 100%;
  min-height: 100%;
}
html {
  position: relative;
}
body {
  padding: 50px 0 0;
  font-family: 'DustismoRegular', sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
.main_wrapper {
  min-height: 100%;
  margin: 0 auto -250px;
}
.main_wrapper .page_start {
  margin: 20px auto 10px;
}
.alert-box > .alert {
  border-left: 0 none;
  border-right: 0 none;
  font-weight: bold;
}
.errormsg {
  color: #a94442;
  font-weight: bold;
}
.scroll-btn {
  width: 75px;
  height: 75px;
  position: fixed;
  right: 3%;
  bottom: 10%;
  z-index: 999;
  border: 2px solid #000;
  border-radius: 8px;
  opacity: 0.4;
}
.caret.caret-large {
  border-top: 8px solid;
  border-right: 8px solid transparent;
  border-bottom: 0 none;
  border-left: 8px solid transparent;
}
.caret.caret-large.active {
  border-top: 0 none;
  border-right: 8px solid transparent;
  border-bottom: 8px solid;
  border-left: 8px solid transparent;
}
/* Links */
a.button-link {
  display: inline-block;
}
/* Panels */
.panel .panel-heading {
  position: relative;
}
.panel .panel-heading .caret {
  position: absolute;
  top: 16px;
  right: 16px;
}
.navbar {
  margin-bottom: 0;
  font-family: 'LillyRegular', 'Arial', sans-serif;
}
.navbar.navbar-gold {
  background-color: #f6dab1;
  border-color: #f1c87a;
}
.navbar-clouds {
  background-color: #9bc5dd;
  background-image: url('/img/bg/sky-header.jpg');
  background-position: center top;
  background-repeat: repeat-x;
  background-attachment: fixed;
  border-color: #9bc5dd;
}
.navbar-brand {
  font-family: 'Corben', Georgia, Times, serif;
  font-size: 2.5em;
}
.navbar .star_logo {
  width: 500px;
  background-image: url('/img/starlogo-small.png');
  background-position: 0 10px;
  background-repeat: no-repeat;
}
.navbar .navbar-brand {
  color: #000;
}
.navbar-nav > li > a {
  color: #333;
}
/***** ~ HOMEPAGE ~ *****/
.content .billboard {
  min-height: 550px;
  padding: 20px 0;
  background-image: url('/img/bg/girl-taking-test.jpg');
  background-attachment: fixed;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
}
.content .billboard .blackboard {
  background-color: rgba(30, 30, 30, 0.6);
  border-radius: 8px;
  padding: 20px;
  margin-top: 20px;
  color: #fff;
}
.content .billboard .blackboard .tagline {
  font-size: 3em;
  font-family: 'CooperHewittBold', 'Futura', sans-serif;
}
.content .billboard .blackboard .tagline > small {
  display: block;
  margin: 15px 0;
  font-size: .5em;
  color: #fff;
  font-weight: normal;
  font-family: 'DustismoRegular', sans-serif;
}
.content .billboard .blackboard ul {
  font-size: 1.25em;
}
.content .billboard .blackboard .button-link {
  margin: 15px 0 0;
}
.content .billboard .blackboard button {
  font-size: 1.5em;
  font-weight: bold;
  font-family: 'CooperHewittBold', 'Futura', sans-serif;
}
.content .features {
  padding: 15px 15px 50px;
}
.content .features header {
  margin-bottom: 10px;
  font-size: 2.5em;
  font-weight: bold;
  font-family: 'CooperHewittBold', 'Futura', sans-serif;
}
.content .features .feature {
  font-size: 1.2em;
}
.content .features .feature.cta-buttons {
  margin-top: 15px;
}
.content .features .feature span {
  margin-left: 5px;
  font-size: 1.5em;
  font-weight: bold;
  font-family: 'CooperHewittSemibold', sans-serif;
}
.content .features .feature button {
  border-radius: 4px;
  font-size: 1.5em;
  font-weight: bold;
  font-family: 'CooperHewittBold', 'Futura', sans-serif;
  text-shadow: 1px -1px 1px rgba(0, 0, 0, 0.5);
  letter-spacing: 1px;
}
.content .features .feature button > i {
  margin-left: 7px;
  vertical-align: middle;
}
.content .features .feature a:hover {
  text-decoration: none;
}
.content .features .feature a.button-link + a.button-link {
  margin-left: 15px;
}
.content .slideshow {
  min-height: 550px;
  padding: 20px 0;
  background-image: url('/img/bg/students-playing-reverse.jpg');
  background-position: center -50px;
  background-repeat: no-repeat;
  background-size: cover;
}
.content .slideshow .levels {
  margin-top: 90px;
  padding: 15px;
  background-color: rgba(30, 30, 30, 0.6);
  border-radius: 8px;
  font-size: 2em;
  font-family: 'CooperHewittBold', 'Futura', sans-serif;
  color: #fff;
  text-align: center;
}
.content .slideshow .levels-grades {
  font-size: 0.9em;
}
.content .slideshow .levels em {
  font-weight: bold;
}
.content .information {
  padding: 20px 0;
  background-color: #f6dab1;
}
.content .information article header {
  font-weight: bold;
  font-size: 1.5em;
}
.content .outer-square-text {
  padding: 15px;
  height: 200px;
  text-align: center;
  cursor: pointer;
}
.content .outer-square-text .inner-square-text {
  padding: 70px 20px 20px;
  height: 100%;
  background-color: #9cc9e0;
  transition: all 0.6s ease-in-out;
}
#testimonial_list {
  margin-top: 20px;
}
#testimonial_list .panel-heading .panel-title {
  font-weight: bold;
}
/***** ~ FOOTER ~ *****/
.footer,
.footer_push {
  height: 250px;
}
.footer {
  width: 100%;
  padding: 20px 0 0;
  color: #fff;
  background-color: #000;
  font-size: 1em;
  font-family: 'LillyRegular', 'Arial', sans-serif;
}
.footer a {
  color: #fff;
  text-decoration: none;
  -webkit-transition: color 0.2s ease-in-out;
  -moz-transition: color 0.2s ease-in-out;
  -o-transition: color 0.2s ease-in-out;
  -ms-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
}
.footer a:hover,
.footer a:focus {
  color: #82AEF8;
}
.footer ul {
  margin: 0;
  padding: 0;
  line-height: 1.6em;
  font-size: .85em;
}
.footer ul.footer-nav {
  font-size: 1.25em;
}
.footer .footer_col {
  padding-bottom: 50px;
}
.footer .footer_col > h3 {
  font-size: 1.3em;
  line-height: 1.3em;
  margin: 0;
}
.footer .footer-btn {
  width: 33px;
  height: 32px;
  margin: 15px 5px 0 !important;
  display: inline-block;
  cursor: pointer;
}
.footer .footer-legal {
  padding-bottom: 10px;
  font-size: .825em;
}
.footer .footer-address {
  margin-top: 7px;
}
#facebook-btn.footer-btn {
  background: url('/img/nav/footer_fb.png') 0 -33px no-repeat;
}
#facebook-btn.footer-btn:hover {
  background-position: 0 0;
}
#google-btn.footer-btn {
  background: url('/img/nav/footer_google.png') 0 -33px no-repeat;
}
#google-btn.footer-btn:hover {
  background-position: 0 0;
}
#linkedin-btn.footer-btn {
  background: url('/img/nav/footer_linkedin.png') 0 -33px no-repeat;
}
#linkedin-btn.footer-btn:hover {
  background-position: 0 0;
}
